import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay, faCircleInfo } from "@fortawesome/free-solid-svg-icons";
import { N_LOGO } from "../utils/constants";

const VideoTitle = ({ title, overview }) => {
  return (
    <div className="w-[40%] aspect-video mx-auto flex items-center p-4 justify-center sm:top-24 xl:top-64 lg:top-40 absolute z-30 videoTitle">
      <div className="text-white flex flex-col w-[90%] mx-auto sm:gap-y-2 xl:gap-y-5 lg:gap-y-3 items-start videoTitle-for-gap">
        <div className="flex items-center justify-center select-none gap-x-2">
          <img
            className="xl:w-[23px] lg:w-[20px] sm:w-[16px] videoTitle-n-logo"
            src={N_LOGO}
            alt="logo1"
          />
          <h1 className="tracking-[0.65rem] text-gray-500 font-bold sm:text-base lg:text-lg  xl:text-xl videoTitle-series">
            SERIES
          </h1>
        </div>
        <h1 className="xl:text-6xl lg:text-5xl sm:text-4xl tracking-wide font-semibold text-red-600 cursor-pointer line-clamp-2 overflow-hidden md:overflow-ellipsis videoTitle-RealTitle">
          {title}
        </h1>
        <p className="select-none overflow-ellipsis lg:text-base xl:text-lg sm:text-xs overflow-hidden sm:line-clamp-3 lg:line-clamp-5 transition-all duration-500 ease-out videoTitle-overview">
          {overview}
        </p>
        <div className="flex sm:gap-x-[2rem] lg:gap-x-[4rem] sm:pl-3 lg:pl-7 videoTitle-btn-container">
          <button className="flex gap-x-[0.40rem] items-center justify-center p-2 bg-white text-black rounded-sm px-7 font-bold hover:scale-110 transition-all duration-200 ease-in-out videoTitle-play-btn">
            <FontAwesomeIcon icon={faPlay} />
            Play
          </button>
          <button className="flex gap-x-[0.40rem] items-center justify-center p-2 bg-[#6d6d6eb3] text-black rounded-sm sm:px-7 font-bold videoTitle-more-btn hover:scale-110 transition-all duration-200 ease-in-out">
            <FontAwesomeIcon icon={faCircleInfo} />
            Info
          </button>
        </div>
      </div>
    </div>
  );
};

export default VideoTitle;
