import React from "react";
import PopupVideoBackground from "./PopupVideoBackground";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlay, faPlus, faXmark } from "@fortawesome/free-solid-svg-icons";

const Popup = ({ video, title }) => {
  const name = title ? title : "Netflix";

  return (
    <div className="absolute top-0 left-0 w-full h-full bg-[#010000ba]">
      <div className="w-full h-[70%] ">
        <PopupVideoBackground movieId={video} />
      </div>
      <div className="w-full h-[30%] flex-col flex items-start">
        <div className=" h-[30%] w-full p-2 flex justify-between gap-x-2">
          <div className="flex w-full h-full gap-x-2 items-start">
            <div className="rounded-full w-[40px] hover:scale-110  duration-200 ease-in-out h-[40px] bg-red-600 hover:bg-white flex items-center justify-center">
              <FontAwesomeIcon className="p-2 text-black" icon={faPlay} />
            </div>

            <div className="rounded-full w-[40px] h-[40px] bg-red-600 flex items-center justify-center hover:scale-110 duration-200 ease-in-out hover:bg-white">
              <FontAwesomeIcon className="p-2 text-black" icon={faPlus} />
            </div>
          </div>
          <div className="rounded-full w-[40px] h-[40px] hover:scale-110 duration-200 ease-in-out bg-black hover:bg-red-600 flex items-center justify-center select-none">
            <FontAwesomeIcon className="p-2 text-white" icon={faXmark} />
          </div>
        </div>
        <p className="text-white p-2 m-2 text-2xl line-clamp-1 overflow-ellipsis font-bold">
          {name}
        </p>
      </div>
    </div>
  );
};

export default Popup;
