import { createSlice } from "@reduxjs/toolkit";

const popupMovieSlice = createSlice({
  name: "popupMovie",
  initialState: {
    popupMovie: null,
  },
  reducers: {
    addPopupMovie: (state, action) => {
      state.popupMovie = action.payload;
    },
  },
});

export const { addPopupMovie } = popupMovieSlice.actions;

export default popupMovieSlice.reducer;
