import React, { useState } from "react";
import { MOVIE_POSTER, N_LOGO } from "../utils/constants";
import Logo from "../utils/images/MOVIE_LOGO.png";
import Popup from "./Popup";

const MovieCard = ({ poster, id, name, overview, onClick, isPopupOpen }) => {
  const [imgSrc, setImgSrc] = useState(MOVIE_POSTER + poster);

  const onError = () => {
    setImgSrc(Logo);
  };

  const handlePopup = () => {
    onClick(id);
  };

  return (
    <div
      onClick={handlePopup}
      className="sm:w-[260px] sm:h-[220px] w-[260px] h-[250px] md:w-[300px] md:h-[270px] lg:w-[360px] lg:h-[320px] xl:w-[400px] xl:h-[380px] z-[25]"
    >
      <div className="scale-100 hover:scale-95 duration-300 w-full h-full">
        <div className="absolute h-[18%] w-[9%] m-2 z-[27]">
          <img className="w-full h-full" src={N_LOGO} alt="" />
        </div>
        <img
          className=" w-full h-full cursor-pointer z-[26] rounded-sm lg:rounded-lg"
          src={imgSrc}
          alt=""
          onError={onError}
        />

        {isPopupOpen && (
          <div className="text-black z-40 absolute top-0 left-0 w-full h-full ">
            <Popup video={id} title={name} desc={overview}></Popup>
          </div>
        )}
      </div>
    </div>
  );
};

export default MovieCard;
