import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import usePopupMovieTrailer from "../hooks/usePopupMovieTrailer";
import LOADING from "../utils/images/MOVIE_LOGO.png";

const PopupVideoBackground = ({ movieId }) => {
  const trailerVideo = useSelector((store) => store?.cardMovies?.popupMovie);
  const [videoLoaded, setVideoLoaded] = useState(false);

  useEffect(() => {
    if (trailerVideo?.key) {
      setVideoLoaded(true);
    }
  }, [trailerVideo]);

  usePopupMovieTrailer(movieId);

  const videoSrc = `https://www.youtube.com/embed/${
    trailerVideo?.key
  }?autoplay=${
    videoLoaded ? 1 : 0
  }&modestbranding=1&controls=0&showinfo=0&playlist=${
    trailerVideo?.key
  }&loop=1`;

  if (!trailerVideo?.key) {
    return (
      <div className="w-full h-full flex  justify-center items-center">
        <img className="p-2 m-2" src={LOADING} alt="Loading" />
      </div>
    );
  }

  return (
    <div className="w-full h-full aspect-video z-30">
      {trailerVideo?.key ? (
        <div className="w-full h-full bg-gradient-to-b from-black relative">
          <iframe
            className="w-full h-full z-10 absolute"
            src={videoSrc}
            key={movieId}
            title="."
            allow="autoplay"
          ></iframe>
          <div className="bg-transparent w-full h-full absolute top-0 z-[11]"></div>
        </div>
      ) : (
        <div></div>
      )}
    </div>
  );
};

export default PopupVideoBackground;
