import React, { useRef } from "react";
import { ReactTyped } from "react-typed";
import lang from "../utils/languageConstants";
import { API_OPTIONS, LANG } from "../utils/constants";
import { useDispatch, useSelector } from "react-redux";
import { changeLanguage } from "../utils/configSlice";
import { addMovieResult } from "../utils/gptSlice";

const GptSearchBar = () => {
  const dispatch = useDispatch();
  const searchText = useRef(null);
  const handleLangChange = (e) => {
    dispatch(changeLanguage(e.target.value));
  };

  const language = useSelector((store) => store.config.lang);

  const searchMovie = async (movie) => {
    const data = await fetch(
      `https://api.themoviedb.org/3/search/movie?query=${movie}&include_adult=false&language=en-US&page=1`,
      API_OPTIONS
    );

    const json = await data.json();
    return json.results;
  };

  const handleGptSearchClick = async () => {
    const movie = searchText.current.value;

    // Call the searchMovie function with the current value of the input
    const movieResults = await searchMovie(movie);
    dispatch(addMovieResult(movieResults));
  };

  return (
    <div className="text-white flex w-full items-center">
      <div className="flex flex-col w-full p-3">
        <div className="flex items-center justify-between w-full md:text-xl lg:text-2xl xl:text-2xl sm:text-base text-xs pl-4">
          <ReactTyped
            strings={[
              "What's your viewing preference at the moment?",
              "Up for some entertainment – what's your pick?",
              "Feeling like indulging in a movie or show – any preferences?",
              "In the mood for some screen time – what would you like to watch?",
              "Ready to unwind with some entertainment – what's your choice?",
              "Looking to enjoy something on screen – any ideas?",
              "Thinking of watching something – any suggestions?",
              "Up for a movie or show – what's on your mind?",
              "Feeling like diving into some entertainment – what's calling your name?",
              "Time for some screen magic – what are you in the mood for?",
            ]}
            typeSpeed={18}
            backSpeed={30}
            loop
            className="text-red-600 font-semibold w-full tracking-wide leading-3"
          />
          <select
            className="bg-transparent text-white text-xs sm:text-sm md:text-sm lg:text-base xl:text-base focus:outline-none focus:bg-transparent pl-2 pr-2"
            onChange={handleLangChange}
          >
            {LANG.map((lang) => (
              <option
                className="bg-transparent text-red-500 text-xs sm:text-sm md:text-sm lg:text-base xl:text-base focus:outline-none focus:bg-transparent"
                value={lang.id}
                key={lang.id}
              >
                {lang.name}
              </option>
            ))}
          </select>
        </div>

        <form
          className="p-4 flex xl:justify-evenly lg:justify-evenly md:justify-evenly sm:justify-evenly justify-between w-full"
          onSubmit={(e) => e.preventDefault()}
        >
          <input
            type="text"
            placeholder={lang[language] ? lang[language].searchPlaceholder : ""}
            ref={searchText}
            className="xl:w-[80%] lg:w-[80%] md:w-[80%] sm:w-[60%] w-[40%] bg-transparent border-2 rounded-lg border-purple-600 pl-3 p-4 text-white focus:outline-none"
          ></input>
          <button
            className="bg-red-600 p-4 rounded-lg xl:w-[15%] lg:w-[15%] md:w-[15%] sm:w-[20%] w-[40%]"
            onClick={handleGptSearchClick}
          >
            {lang[language] ? lang[language].search : ""}
          </button>
        </form>
      </div>
    </div>
  );
};

export default GptSearchBar;
