import { useDispatch } from "react-redux";
import { API_OPTIONS } from "../utils/constants";
import { addPopupMovie } from "../utils/popupMovieSlice";
import { useEffect } from "react";

const usePopupMovieTrailer = (movieId) => {
  const dispatch = useDispatch();

  useEffect(() => {
    const getMovieVideos = async () => {
      const data = await fetch(
        `https://api.themoviedb.org/3/movie/${movieId}/videos?language=en-US`,
        API_OPTIONS
      );
      const json = await data.json();

      const filterData = json.results.filter(
        (video) =>
          video?.name.includes("Trailer") && video?.type.includes("Trailer")
      );
      const trailer = filterData.length === 0 ? filterData[0] : json.results[0];
      dispatch(addPopupMovie(trailer));
    };

    getMovieVideos();
  }, [movieId, dispatch]); // Add dispatch to the dependency array
};

export default usePopupMovieTrailer;
