import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import useMovieTrailer from "../hooks/useMovieTrailer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faVolumeMute, faVolumeHigh } from "@fortawesome/free-solid-svg-icons";
import { N_LOGO } from "../utils/constants";

const VideoBackground = ({ movieId }) => {
  const trailerVideo = useSelector((store) => store?.movies?.trailerVideo);
  const [videoLoaded, setVideoLoaded] = useState(false);
  const [mute, setMute] = useState(true);

  useEffect(() => {
    if (trailerVideo?.key) {
      setVideoLoaded(true);
    }
  }, [trailerVideo]);

  const handleMute = () => {
    setMute(!mute);
  };

  useMovieTrailer(movieId);

  const videoSrc = `https://www.youtube.com/embed/${
    trailerVideo?.key
  }?autoplay=${videoLoaded ? 1 : 0}&mute=${
    mute ? 1 : 0
  }&modestbranding=1&controls=0&showinfo=0&playlist=${
    trailerVideo?.key
  }&loop=1`;

  if (!trailerVideo?.key) {
    return (
      <div className="w-full h-full flex  justify-center items-center">
        <img className="p-2 m-2" src={N_LOGO} alt="Loading" />
      </div>
    ); // Replace this with your actual loading component
  }

  return (
    <div className="w-full h-full aspect-video">
      {trailerVideo?.key ? (
        <div className="w-full h-full bg-gradient-to-b from-black relative">
          <iframe
            className="w-full h-full z-10 absolute"
            src={videoSrc}
            key={movieId}
            title="."
            allow="autoplay"
          ></iframe>
          <div className="w-full h-full absolute z-20">
            <button
              onClick={handleMute}
              className="text-white sm:text-xs  lg:text-xl border bg-transparent border-[#6d6d6eb3] z-30 p-2 absolute right-32 sm:bottom-40 md:bottom-60 lg:bottom-72 xl:bottom-96 rounded-full videoMuteButton "
            >
              {mute ? (
                <FontAwesomeIcon icon={faVolumeMute} />
              ) : (
                <FontAwesomeIcon icon={faVolumeHigh} />
              )}
            </button>
          </div>
        </div>
      ) : (
        <div></div>
      )}
    </div>
  );
};

export default VideoBackground;
