import { useDispatch } from "react-redux";
import { API_OPTIONS } from "../utils/constants";
import { addAiringTodayTv } from "../utils/moviesSlice";
import { useEffect, useState } from "react";

const useAiringTodayTv = () => {
  const [page, setPage] = useState(getRandomPage());
  const dispatch = useDispatch();

  function getRandomPage() {
    return Math.floor(Math.random() * 9) + 1;
  }

  useEffect(() => {
    const randomPage = getRandomPage();
    setPage(randomPage);
  }, []);

  const getAiringTodayTv = async () => {
    const data = await fetch(
      `https://api.themoviedb.org/3/tv/airing_today?language=en-US&page=${page}`,
      API_OPTIONS
    );
    const json = await data.json();
    dispatch(addAiringTodayTv(json.results));
  };

  useEffect(() => {
    getAiringTodayTv();
  }, []);
};

export default useAiringTodayTv;
