import React from "react";
import { useSelector } from "react-redux";
import VideoTitle from "./VideoTitle";
import VideoBackground from "./VideoBackground";

const MainContainer = ({ movieId }) => {
  const movies = useSelector((store) => store?.movies?.nowPlayingMovies);

  //early return
  if (!movies)
    return (
      <div className="w-full h-full">
        <div className="w-full lg:h-[60vh] md:h-[50vh] h-[30vh] xl:h-screen bg-gray-900 relative overflow-hidden">
          <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white to-transparent shimmer"></div>
        </div>

        {/* yt shimer */}
        <div></div>
      </div>
    );

  // Find the movie with the passed movieId
  let mainMovie = movies.find((movie) => movie.id === movieId);

  // If no movie was found with the passed movieId, select a random movie
  if (!mainMovie) {
    const randomIndex = Math.floor(Math.random() * movies?.length);
    mainMovie = movies[randomIndex];
  }

  const { original_title, overview, id } = mainMovie;

  return (
    <div className="w-full  aspect-video">
      <VideoBackground movieId={id}></VideoBackground>
      <VideoTitle title={original_title} overview={overview}></VideoTitle>
    </div>
  );
};

export default MainContainer;
