import { useEffect, useRef, useState } from "react";
import MovieCard from "./MovieCard";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";

const MovieList = ({ title, movies, onClick, selectedMovieId }) => {
  const scrollRef = useRef();
  const [isAtStart, setIsAtStart] = useState(true);
  const [isAtEnd, setIsAtEnd] = useState(false);
  const [isHovered, setIsHovered] = useState(false);

  const handleCardClick = (id) => {
    if (onClick) {
      onClick(id);
    } else {
      alert("Only movies have the trailer, not TV show");
    }
  };

  const checkScrollPosition = () => {
    setIsAtStart(scrollRef.current.scrollLeft === 0);
    setIsAtEnd(
      scrollRef.current.scrollLeft + scrollRef.current.offsetWidth >=
        scrollRef.current.scrollWidth
    );
  };

  useEffect(() => {
    const currentScrollRef = scrollRef.current;
    checkScrollPosition();
    currentScrollRef.addEventListener("scroll", checkScrollPosition);
    return () =>
      currentScrollRef.removeEventListener("scroll", checkScrollPosition);
  }, [movies]);

  const scroll = (scrollOffset) => {
    scrollRef.current.scrollTo({
      left: scrollRef.current.scrollLeft + scrollOffset,
      behavior: "smooth",
    });
  };

  return (
    <div className="flex flex-col z-30 pl-10 pb-3 pt-9 group">
      <h1 className="xl:text-2xl pb-3 font-semibold z-30 text-[#999999] select-none MovieList-title flex items-center">
        {title}
        <div
          className="flex items-center text-xs p-3 cursor-pointer"
          onMouseEnter={() => setIsHovered(true)}
          onMouseLeave={() => setIsHovered(false)}
        >
          <FontAwesomeIcon icon={faChevronRight} className="mr-2" />
          <span
            className={`opacity-0 ${
              isHovered ? "opacity-100" : ""
            } ease-in-out text-xs text-[#6fb7c3] duration-300 transition-all`}
          >
            Explore All
          </span>
        </div>
      </h1>

      <div
        className="flex overflow-x-scroll overflow-y-hidden custom-scroll"
        ref={scrollRef}
      >
        <div className="flex gap-x-3 items-center justify-center group">
          {!isAtStart && (
            <button
              className="opacity-0 group-hover:opacity-100 text-white absolute ml-10 w-[40px] bg-gradient-to-r text-3xl pl-3 from-[#141414a8] duration-500 ease-in-out transition-all h-[360px] left-0 z-40"
              onClick={() => scroll(-1000)}
            >
              <FontAwesomeIcon
                className="hover:scale-125 duration-200 hover:text-red-500 ease-in-out transition-all"
                icon={faChevronLeft}
              />
            </button>
          )}
          {movies &&
            movies.map((movie) => {
              return (
                <MovieCard
                  key={movie.id}
                  id={movie.id}
                  poster={movie.poster_path}
                  name={movie.name || movie.original_name || movie.title}
                  overview={movie.overview}
                  onClick={handleCardClick}
                  isPopupOpen={selectedMovieId === movie.id}
                />
              );
            })}
          {!isAtEnd && (
            <button
              className="opacity-0 group-hover:opacity-100 absolute w-[40px] text-white bg-gradient-to-l from-[#141414a8] text-3xl pr-3 right-0 h-[360px] z-40 transition-all"
              onClick={() => scroll(1000)}
            >
              <FontAwesomeIcon
                className="hover:scale-125 hover:text-red-500 duration-200 ease-in-out transition-all"
                icon={faChevronRight}
              />
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default MovieList;
