import React, { useEffect } from "react";
import BrowserHeader from "./BrowserHeader";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CommonToAll from "./CommonToAll";
import MainContainer from "./MainContainer";
import SecondaryContainer from "./SecondaryContainer";
import usePopularMovies from "../hooks/usePopularMovies";
import useNowPlayingMovies from "../hooks/useNowPlayingMovies";
import useTopRatedMovies from "../hooks/useTopRatedMovies";
import useUpcomingMovies from "../hooks/useUpcomingMovies";
import useAiringTodayTv from "../hooks/useAiringTodayTv";
import useOnTheAirTv from "../hooks/useOnTheAirTv";
import usePopularTv from "../hooks/usePopularTv";
import useTopRatedTv from "../hooks/useTopRatedTv";
import GptModule from "./GptModule";
import { useSelector } from "react-redux";

const Browse = () => {
  useNowPlayingMovies();
  usePopularMovies();
  useTopRatedMovies();
  useUpcomingMovies();
  useAiringTodayTv();
  useOnTheAirTv();
  usePopularTv();
  useTopRatedTv();

  const showGptSearch = useSelector((store) => store.gpt.showGptSearch);

  const currentMovieId = useSelector((store) => store.movies.currentMovieId); // Replace with actual selector

  useEffect(() => {
    toast("Login Succesfully !");
  }, []);

  return (
    <div className="bg-[#141414] w-full min-h-[100vh]">
      <CommonToAll></CommonToAll>
      <BrowserHeader></BrowserHeader>
      <div className="absolute flex top-14 w-full items-center justify-center ">
        {showGptSearch && <GptModule></GptModule>}
      </div>

      <ToastContainer
        position="top-right"
        autoClose={1800}
        limit={1}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss={false}
        draggable
        pauseOnHover={false}
        theme="dark"
      />
      <div className="flex w-full h-full -z-10">
        <MainContainer movieId={currentMovieId}></MainContainer>
      </div>

      <SecondaryContainer></SecondaryContainer>
    </div>
  );
};

export default Browse;
