import React, { useState } from "react";
import { useSelector } from "react-redux";
import MovieCard from "./MovieCard";

const GptMovieSuggestions = () => {
  const movieResults = useSelector((store) => store.gpt.tmdbMovies);
  const [selectedMovieId, setSelectedMovieId] = useState(null);

  const handleCardClick = (id) => {
    setSelectedMovieId((prevId) => (prevId === id ? null : id));
  };

  if (!movieResults) return;

  return (
    <div className="h-screen overflow-y-hidden">
      <div className="h-full">
        <h2 className="text-white text-3xl p-2 text-center font-bold">
          Movie Suggestions
        </h2>
        <div className="h-full overflow-y-scroll custom-scroll m-4 p-2">
          {movieResults.map((movie) => (
            <div key={movie.id} className="flex justify-evenly w-full">
              <MovieCard
                poster={movie.poster_path}
                id={movie.id}
                name={movie.original_title}
                overview={movie.overview}
                onClick={handleCardClick}
                isPopupOpen={movie.id === selectedMovieId}
              ></MovieCard>
              <div className="flex flex-col w-[40%] p-1 items-start justify-evenly text-white">
                <h1 className="text-red-600 font-bold text-6xl line-clamp-1 overflow-ellipsis">
                  {movie.title}
                </h1>
                <h2 className="sm:text-lg md:text-lg lg:text-lg xl:tex-lg text-base font-medium line-clamp-4 overflow-ellipsis leading-7">
                  {movie.overview}
                </h2>
                <p>{movie.release_date}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default GptMovieSuggestions;
