import React, { useRef, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPowerOff,
  faUser,
  faCaretDown,
  faMagnifyingGlass,
} from "@fortawesome/free-solid-svg-icons";
import { signOut } from "firebase/auth";
import { auth } from "../utils/firebase";
import { useDispatch, useSelector } from "react-redux";
import { LOGO, USER_LOGO } from "../utils/constants";
import { toggleGptSearchView } from "../utils/gptSlice";

const BrowserHeader = () => {
  const [showDialog, setShowDialog] = useState(false);
  const arrowRef = useRef(null);
  const dialogRef = useRef(null);
  const dispatch = useDispatch();
  const user = useSelector((store) => store.user);
  const [searchDialog, setSearchDialog] = useState(false);

  const handleSignOut = () => {
    signOut(auth)
      .then(() => {})
      .catch((error) => {});
  };

  const handleArrowMouseEnter = () => {
    setShowDialog(true);
  };

  const handleArrowMouseLeave = () => {
    setTimeout(() => {
      if (dialogRef?.current?.contains(document.activeElement)) {
        setShowDialog(false);
      }
    }, 300);
  };

  const handleDialogMouseEnter = () => {
    setShowDialog(true);
  };

  const handleDialogMouseLeave = () => {
    setShowDialog(false);
  };

  const handleClick = () => {
    setShowDialog(!showDialog);
  };

  const handleGptSearchClick = () => {
    dispatch(toggleGptSearchView());
    setSearchDialog(!searchDialog);
  };

  return (
    <div className="flex w-full items-center justify-center bg-gradient-to-b from-[#00000097] z-30 absolute">
      <div className="w-[90%] m-4 flex justify-between items-center">
        <div className="w-[40%]">
          <img
            src={LOGO}
            alt="logo"
            className="max-w-[92.49px] max-h-[29.50px]"
          />
        </div>
        <button
          className="flex items-center justify-items-center"
          onClick={handleGptSearchClick}
        >
          <div className=" bg-purple-700 py-1 px-3 flex items-center justify-center gap-x-2 text-white rounded-md">
            <FontAwesomeIcon icon={faMagnifyingGlass} />
            Search AI
          </div>
          <FontAwesomeIcon
            className={`text-white transition-all ml-2 duration-300 ease-in-out ${
              searchDialog ? "rotate-180" : ""
            }`}
            icon={faCaretDown}
          />
        </button>
        <div>
          <div
            ref={arrowRef}
            onMouseEnter={handleArrowMouseEnter}
            onMouseLeave={handleArrowMouseLeave}
            onClick={handleClick}
            className="flex gap-x-2 cursor-pointer  items-center justify-center"
          >
            <div>
              <img src={USER_LOGO} alt="" />
            </div>
            <FontAwesomeIcon
              className={`text-white transition-all duration-300 ease-in-out ${
                showDialog ? "rotate-180" : ""
              }`}
              icon={faCaretDown}
            />
          </div>
          {showDialog && (
            <div
              ref={dialogRef}
              onMouseEnter={handleDialogMouseEnter}
              onMouseLeave={handleDialogMouseLeave}
              className="absolute bg-[#141414a8] text-white p-4 mt-2 right-11 rounded-md shadow-md"
            >
              <p className="font-bold flex text-white text-xs items-center justify-center gap-x-2 cursor-pointer hover:underline">
                <FontAwesomeIcon icon={faUser} />
                {user?.displayName}
              </p>
              <button
                onClick={handleSignOut}
                className="text-white flex items-center justify-center text-xs gap-x-2 hover:underline mt-2"
              >
                <FontAwesomeIcon icon={faPowerOff} />
                Sign Out
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default BrowserHeader;
