import React, { useEffect, useState } from "react";
// import { START_GIF } from "./constants";
import GIF from "./images/START.gif";

const GifDisplay = () => {
  const [showGif, setShowGif] = useState(true);

  useEffect(() => {
    const timeoutId = setTimeout(() => {
      setShowGif(false);
      //   setShowLogin(true);
    }, 2500); // Adjust the duration (in milliseconds) as needed

    return () => {
      clearTimeout(timeoutId);
    };
  }, []);

  return (
    <div style={{ display: showGif ? "block" : "none" }}>
      {/* Replace the gifUrl with your actual gif URL */}
      <img
        src={GIF}
        alt="Netflix startup gif"
        style={{
          width: "100%",
          height: "100%",
          position: "fixed",
          top: 0,
          left: 0,
          zIndex: 9999,
        }}
      />
    </div>
  );
};

export default GifDisplay;
