import React, { useState } from "react";
import MovieList from "./MovieList";
import { useSelector } from "react-redux";

const SecondaryContainer = () => {
  const movies = useSelector((store) => store.movies);
  const [selectedMovieId, setSelectedMovieId] = useState(null);

  const handleCardClick = (id) => {
    setSelectedMovieId((prevId) => (prevId !== id ? id : null));
  };

  return (
    <div>
      <div className="lg:-mt-36 md:-mt-36 sm:-mt-20 xl:-mt-56 z-30 secondary-container">
        <MovieList
          title={"Now Playing Movies"}
          movies={movies.nowPlayingMovies}
          onClick={handleCardClick}
          selectedMovieId={selectedMovieId}
        />
        <MovieList
          onClick={handleCardClick}
          selectedMovieId={selectedMovieId}
          title={"Popular Movies"}
          movies={movies.popularMovies}
        />
        <MovieList
          title={"Top Rated Movies"}
          movies={movies.topRatedMovies}
          onClick={handleCardClick}
          selectedMovieId={selectedMovieId}
        />
        <MovieList
          title={"Up Coming Movies"}
          movies={movies.upComingMovies}
          onClick={handleCardClick}
          selectedMovieId={selectedMovieId}
        />
        <MovieList title={"Airing Today Tv"} movies={movies.airingTodayTv} />
        <MovieList title={"On The Air Tv"} movies={movies.onTheAirTv} />
        <MovieList title={"Popular Tv"} movies={movies.popularTv} />
        <MovieList title={"Top Rated Tv"} movies={movies.topRatedTv} />
      </div>
    </div>
  );
};

export default SecondaryContainer;
