import React from "react";
import { LOGO } from "../utils/constants";

const Header = () => {
  return (
    <div className="w-full flex items-center md:px lg:px-40 lg:py-4 sm:px-20 sm:py-2 header">
      <img
        src={LOGO}
        alt="logo"
        className=" sm:p-3 sm:w-[15%] sm:h-[110%] z-10 logo"
      />
    </div>
  );
};

export default Header;
