import React, { useRef, useState } from "react";
import Header from "./Header";
import { checkValidData } from "../utils/validate";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleXmark } from "@fortawesome/free-regular-svg-icons";
import {
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  GoogleAuthProvider,
  signInWithPopup,
  updateProfile,
  sendPasswordResetEmail,
} from "firebase/auth";
import { auth } from "../utils/firebase";
import { useDispatch } from "react-redux";
import { addUser } from "../utils/userSlice";
import CommonToAll from "./CommonToAll";

const Login = () => {
  const [isSignInForm, setIsSignInForm] = useState(true);
  const [errorMessage, setErrorMessage] = useState(null);

  const dispatch = useDispatch();
  const name = useRef(null);
  const email = useRef(null);
  const password = useRef(null);
  const provider = new GoogleAuthProvider();

  const toggleSignInForm = () => {
    setIsSignInForm(!isSignInForm);
  };

  const handleForgotPassword = () => {
    const enteredEmail = email.current.value;
    if (!enteredEmail) {
      alert("Email required");
      return;
    }

    sendPasswordResetEmail(auth, enteredEmail)
      .then(() => {
        alert(`Password reset link has been sent to ${enteredEmail}`);
      })
      .catch((error) => {
        alert(`Error: ${error.message}`);
      });
  };

  const handleButtonClick = () => {
    //First of all we will validate the form data
    const message = checkValidData(email.current.value, password.current.value);
    setErrorMessage(message);
    setTimeout(() => {
      setErrorMessage(null);
    }, 5000);

    if (message !== null) return;

    //Sign In / Sign Up Logic
    if (!isSignInForm) {
      //Sign Up Logic
      createUserWithEmailAndPassword(
        auth,
        email.current.value,
        password.current.value
      )
        .then((userCredential) => {
          // Signed up
          const user = userCredential.user;
          // navigate("/browse");
          updateProfile(auth.currentUser, {
            displayName: name?.current?.value,
          })
            .then(() => {
              const { uid, email, displayName } = auth.currentUser;
              dispatch(
                addUser({ uid: uid, email: email, displayName: displayName })
              );
            })
            .catch((error) => {
              setErrorMessage(error.message);
            });

          // console.log(user);
        })
        .catch((error) => {
          setErrorMessage("Error: Email already in use!");
        });
    } else {
      //Sign In Logic
      signInWithEmailAndPassword(
        auth,
        email.current.value,
        password.current.value
      )
        .then((userCredential) => {
          // Signed in

          const user = userCredential.user;
          // navigate("/browse");
          updateProfile(user, {
            displayName: name?.current?.value,
          })
            .then(() => {
              const { uid, email, displayName } = auth.currentUser;
              dispatch(
                addUser({ uid: uid, email: email, displayName: displayName })
              );
            })
            .catch((error) => {
              setErrorMessage(error.message);
            });
        })
        .catch((error) => {
          setErrorMessage("Error : Invalid Credentials");
        });
    }
  };

  const handleGoogleSignIn = () => {
    signInWithPopup(auth, provider)
      .then((result) => {
        // This gives you a Google Access Token. You can use it to access the Google API.
        const credential = GoogleAuthProvider.credentialFromResult(result);
        updateProfile(auth.currentUser, {
          displayName: name?.current?.value,
        })
          .then(() => {
            const { uid, email, displayName } = auth.currentUser;
            dispatch(
              addUser({ uid: uid, email: email, displayName: displayName })
            );
          })
          .catch((error) => {
            setErrorMessage(error.message);
          });
      })
      .catch((error) => {});
  };

  return (
    <div className="flex flex-col login-page w-[100vw] h-[100vh] items-center -z-10">
      <CommonToAll></CommonToAll>
      <Header></Header>
      <div className="sm:w-[450px] sm:h-[710px] flex flex-col items-center bg-[#0100009c] justify-center z-10 form">
        <form
          onSubmit={(e) => e.preventDefault()}
          className="flex flex-col items-start sm:w-[80%] h-[90%] form-main z-10"
        >
          <h1 className="text-3xl font-bold pb-6 text-white select-none">
            {isSignInForm ? "Sign In" : "Sign Up"}
          </h1>
          {!isSignInForm && (
            <input
              ref={name}
              type="text"
              placeholder="Full Name"
              required
              className="z-10 p-4 m-2 sm:w-[95%] form-input text-white bg-[#0f0e0e] border border-gray-600 rounded-md"
            />
          )}
          <input
            ref={email}
            type="text"
            placeholder="Email Address"
            required
            className="sz-10 p-4 m-2 sm:w-[95%] form-input text-white bg-[#0f0e0e] border border-gray-600 rounded-md "
          />
          <input
            ref={password}
            type="password"
            placeholder="Password"
            required
            className="p-4 z-10 m-2  form-input sm:w-[95%] text-white bg-[#0f0e0e] border border-gray-600 rounded-md"
          />
          <button
            onClick={handleButtonClick}
            className="z-10 bg-red-600 hover:bg-red-700 p-2 rounded-md duration-200 text-white font-semibold m-2 form-input sm:w-[95%] ease-in"
          >
            {isSignInForm ? "Sign In" : "Sign Up"}
          </button>
          <button
            onClick={handleGoogleSignIn}
            class="form-input m-2 p-2 flex justify-center items-center bg-red-600 hover:bg-red-700 text-white sm:w-[95%] transition ease-in duration-200 text-center text-base font-semibold rounded-md"
          >
            <svg
              width="20"
              height="20"
              fill="currentColor"
              class="mr-2"
              viewBox="0 0 1792 1792"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M896 786h725q12 67 12 128 0 217-91 387.5t-259.5 266.5-386.5 96q-157 0-299-60.5t-245-163.5-163.5-245-60.5-299 60.5-299 163.5-245 245-163.5 299-60.5q300 0 515 201l-209 201q-123-119-306-119-129 0-238.5 65t-173.5 176.5-64 243.5 64 243.5 173.5 176.5 238.5 65q87 0 160-24t120-60 82-82 51.5-87 22.5-78h-436v-264z"></path>
            </svg>
            Sign in with Google
          </button>
          <p className="text-red-500 font-semibold p-1 text-center text-xl">
            {errorMessage && (
              <>
                <FontAwesomeIcon icon={faCircleXmark} /> {errorMessage}
              </>
            )}
          </p>
          <p
            className="text-white text-center p-1 cursor-pointer hover:underline hover:text-[#9aa6b0]"
            onClick={handleForgotPassword}
          >
            Forgot Password?
          </p>
          <div className="w-full mt-20 flex flex-col items-start ">
            <label className="text-white flex items-center justify-center gap-x-3 ">
              <input
                type="checkbox"
                className="h-5 w-5 border duration-200 ease-in-out bg-black text-black cursor-pointer hover:border-gray-400 checked:border-transparent focus:outline-none rounded-sm appearance-none checked:bg-gray-400 "
              />
              Remember me
            </label>
            <p className="text-[#b4b6b5] mt-3 p-1 select-none">
              {isSignInForm ? "New to Netflix?" : "Already registered?"}{" "}
              <span
                onClick={toggleSignInForm}
                className="cursor-pointer hover:underline text-white"
              >
                {isSignInForm ? "Sign up now." : "Sign in."}
              </span>
            </p>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Login;
